import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { selectDingpanFile } from '../actions';

export default function DingpanMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;
  const { value } = controller;
  const isDisabled = controller.query('isSelectionInImage') || controller.query('isSelectionInLinkCard');

  const handleClick = () => {
    if (isDisabled) {
      return;
    }
    controller.run('onAction', selectDingpanFile());
  };

  return (
    <MenuItem {...rest} onClick={handleClick} disabled={isDisabled} role={PluginRoles.refer}>
      {title || ''}
    </MenuItem>
  );
}
